import React, { Component } from 'react';

class Frais extends Component<{}, {taxin: number, valuegiven: number, tenpercent: number, twentysixpercent: number, sevendayslate: number, onepercent: number, currentDateTime: string, forteendayslate: number}> {

  constructor (props:any){

    var today = new Date(),

    date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();

    super(props);
    this.state = {
      taxin: 1,
      valuegiven: 1,
      tenpercent: 0,
      twentysixpercent: 0,
      sevendayslate: 0,
      forteendayslate: 0,
      onepercent: 0,
      currentDateTime: date
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e:any){
    this.setState({
      valuegiven: e.target.value,
      tenpercent: +(e.target.value*1.10).toPrecision(4),
      twentysixpercent: +(e.target.value*1.26).toPrecision(5),
      sevendayslate: +(e.target.value*1.33).toPrecision(5),
      forteendayslate: +(e.target.value*1.40).toPrecision(5),
      onepercent: +(e.target.value*0.01).toPrecision(5) 
    });
  }

/*

calcul les frais pour 10/26 puis jusquau jours actuel et puis le co√ªt de frais chaque jours 1% 
-- besoin de la date de d√©but du contrat pour calculer les reste des frais
*/
render(){
    return(
    <>
     <header className="App-header">
      <label htmlFor="taxout">Montant donn√© :</label>
      <input type="number" id="taxout" autoFocus  name="taxout" value={this.state.valuegiven} onChange={this.handleChange}></input>
      <label htmlFor="taxin">10% : {this.state.tenpercent}</label>
      <label htmlFor="taxin">26% : {this.state.twentysixpercent} </label>
      <label htmlFor="taxin">7 jours de retard : {this.state.sevendayslate}</label>
      <label htmlFor="taxin">14 jours de retard : {this.state.forteendayslate}</label>
      <label htmlFor="taxin">1% par jour : {this.state.onepercent} </label>
      {/*<label htmlFor="taxin">Montant pour : {this.state.currentDateTime} :  </label>*/}

    </header>
    </>
    )
  }
}
export default Frais;
