import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import logo from './img/DM-Logo-sm.png'; 

class DM extends Component<{}, {}> {


  constructor (props: {} | Readonly<{}>){
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }


  handleClick(){
    window.open('https://www.linkedin.com/in/gatienboquet/', '_blank');
  }

  render(){

    return(
      <>
        <div className="intro">
          <img className="center" height={250} width={350} src={logo}></img>
          <h1>Dubstep Mag - Le m√©dia 100% dubstep</h1>
          <h2>Version actuel : CMS Wordpress</h2>
          <h2>Version 3.0 : D√©veloppement en cours</h2>
          <h2>Stack frontend : React, TypeScript, GraphQL, LESS, WebPack </h2>
          <h2>Stack Backend : Node.js</h2>
          <h3>Lien : <a href="www.dubstepmag.com">www.dubstepmag.com</a></h3>

          
        </div>
        
      </>
    )
  }
}

export default DM;
