import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import crmimg from './img/crm.png'; 

class crm extends Component<{}, {}> {


  constructor (props: {} | Readonly<{}>){
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }


  handleClick(){
    window.open('https://github.com/GatienBoquet/crm-react-firebase', '_blank');
  }

  render(){

    return(
      <>
        <div className="intro">
          <img className="center" height={250} width={350} src={crmimg}></img>
          <h1>Un simple CRM avec React.JS / Materialize et FireBase comme backend</h1>
          <h2>Liste de fonctionnalit√©s</h2>
          <h2> Affichage des items</h2>
          <h2> Ajout d'items</h2>
          <h2> Suppression d'items</h2>
          <h3> Modifcation d'items</h3>
          <a href="https://github.com/GatienBoquet/crm-react-firebase">GitHub crm-react-firebase</a>
          
        </div>
        
      </>
    )
  }
}

export default crm;
