import React, { Component } from 'react';
import './index.css';

class Profit extends Component<{}, {taxin: number, taxout: number,tps: number, tvq: number, prix_achat: number, prix_vente:number, profit: number}> {
    constructor (props:any){
        super(props);
        this.state = {
          taxin: 1,
          taxout: 1,
          tps: 0.05,
          tvq: 0.09975,
          prix_achat: 0,
          prix_vente: 0,
          profit: 0
        };
        this.toTaxIn = this.toTaxIn.bind(this);
        this.taxoutChange = this.taxoutChange.bind(this);
        this.setPrixAchat = this.setPrixAchat.bind(this);
        this.setPrixVente = this.setPrixVente.bind(this);
        this.calcul_profit = this.calcul_profit.bind(this);
      }
    
      toTaxIn(e:any){
        var tps:number = (e.target.value * (this.state.tps));
        var tvq = (e.target.value * (this.state.tvq));
        var total:number = (parseFloat(e.target.value) + tps + tvq);
        total = total || 0;
        this.setState({
          taxin: +total.toPrecision(5),
          taxout: e.target.value
        });
      }
    
      taxoutChange(e:any){
        var tps = (e.target.value * (this.state.tps));
        var tvq = (e.target.value * (this.state.tvq));
        var total = (parseFloat(e.target.value) / 1.14975);
        total = total+0.01 || 0
        this.setState({
          taxin: e.target.value,
          taxout: +total.toPrecision(5)
    
        });
      }

      setPrixAchat(e:any){
        this.setState({
          prix_achat: e.target.value,
    
        });
      }

      
      setPrixVente(e:any){
        this.setState({
          prix_vente: e.target.value,
    
        });
      }

      
      calcul_profit(){
        var prix = this.state.prix_achat || 0
        var prix_vente = this.state.prix_vente || 0
        var total =  prix_vente - prix;
        this.setState({
          profit: total,
        });
      }


    render(){
        return(
        <>
         <header className="App-header">
          <label htmlFor="taxout">Prix d'achat :</label>
          <input type="number" id="taxout" name="taxout" value={this.state.prix_achat} onChange={this.setPrixAchat}></input>
          <label htmlFor="taxin">Prix vente TAX IN</label>
          <input type="number" id="taxin" name="taxin" value={this.state.prix_vente} onChange={this.setPrixVente} onKeyUp={this.calcul_profit} ></input>
          <label  htmlFor="prixfinal">Profit :</label><a style={{backgroundColor: 'blue'}} id="prixfinal" >{this.state.profit}</a> 
        </header>
        </>
        )
      }
    }

export default Profit;
